import React, { Component } from 'react';
import api from 'util/ApiAdonis';
//import { Tag } from 'antd';
import LeftList from 'components/LeftList';
import EllipsisTooltip from 'components/EllipsisTooltip';
//var _ = require('lodash');

const columns = [
  {
    title: 'Nmero Pedido Venda',
    dataIndex: 'id',
    key: 'id',
    //sortOrder: 'descend',
    render: (text) => text,
  },
  {
    title: 'Status',
    dataIndex: 'status',
    key: 'status',
    onCell: () => {
      return {
        style: {
          whiteSpace: 'nowrap',
          maxWidth: 150,
        },
      };
    },
    render: (text) => <EllipsisTooltip title={text}>{text}</EllipsisTooltip>,
  },
];

const model = 'salesorder';

class ListUsers extends Component {
  constructor(props) {
    super();
    console.log('ListUSers - Constructor');
    console.log(props);

    this.state = {
      data: [],
    };
  }

  componentDidMount() {
    console.log('ComponentDidMout - CustomerList');
    this.fetchLeftList();
  }

  //sort=cod ASC&limit=999&populate=false
  fetchLeftList = () => {
    console.log('fecthLeftlist');
    api
      .get(`${model}`, {})
      .then((result) => {
        console.log(result);
        this.setState({
          data: result.data.data,
        });
      })
      .catch(function (error) {
        console.log('error', error);
      });
  };

  render() {
    return (
      <LeftList
        columns={columns}
        data={this.state.data}
        onRowClick={(record, rowIndex) => {
          return {
            onClick: (event) => this.props.onClickRow(record, rowIndex),
          };
        }}
      />
    );
  }
}

export default ListUsers;
