export const columns = [
  {
    title: 'Maq.',
    dataIndex: 'maquina',
    render: (text, obj) => {
      if (obj.maquina !== '') return obj.maquina;
      else return obj.montagem;
    },
  },
  {
    title: 'Codigo Produto',
    dataIndex: 'cod',
  },
  {
    title: 'Quantidade',
    dataIndex: 'qtde',
    align: 'center',
  },
  {
    title: 'Status',
    dataIndex: 'statusEtapa',
    render: (text) => {
      return <span className="gx-text-red">{text}</span>;
    },
  },
];

